<template>
  <div class="creditsvideo" id="creditsvideo">
    <div class="cover"></div>
    
      <div :class="`overlay ${hideTransition ? `hideTransition` : ``}`">
        <transition appear name="fadein">
          <h1>C R E D I T S</h1>
        </transition>
      </div>

      <youtube 
        v-if="link"
        id="video" 
        :video-id="link" 
        :player-vars="{ autoplay: hideTransition ? 1 : 0, rel: 0, modestbranding: 1 }" 
        
        @ended="ended"
        >
      </youtube>
   <!-- </transition> -->
  </div>
</template>

<script>
// import prismic from "../prismic.js";

export default {
  name: 'VinylVideo',
  components: {
  },
  data() {
    return {
      
      hideTransition: false,
      link: this.$cms.textField(this.$store.state.prismicData.credits_video_youtube_id)
    }
  },
  props: {
    mousemoveObj: {
      type: Object,
      default: () => {}
    },
    clickObj: {
      type: Object,
      default: () => {}
    },
  },
  watch: {
  },
  methods: {
    ended() {
      window.location.href = "./index.html#/welcome";
    },
  },
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.hideTransition = true;
      }, 1000);
    })
  },
  beforeMount() {
    // in case someone tries accessing via link and they are offline
    if (!window.navigator.onLine) window.location.href = "./index.html";
  }
}
</script>

